.messages__list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 1rem; }

.messages__item {
  padding: 0.3rem; }

.messages__user, .messages__text {
  width: fit-content;
  padding: 0.1rem 0.5rem;
  border-radius: 0.3rem;
  border: 0; }

.messages__user {
  margin: 0 1rem;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  padding-top: 0.5rem;
  padding-bottom: 0;
  font-weight: bold;
  text-transform: uppercase; }
