.chat {
  display: grid;
  height: 100%;
  min-width: 20rem;
  grid-gap: 0.3rem;
  grid-template-areas: "h h" "u m" "f f";
  grid-template-columns: minmax(auto, 200px) 1fr;
  grid-template-rows: minmax(50px, auto) 1fr minmax(100px, auto); }
  @media screen and (max-width: 30rem) {
    .chat {
      grid-template-columns: auto 1fr; } }
  .chat__header {
    grid-area: h;
    background-color: #ffe8e8;
    border: 0.1rem solid #ffdede;
    border-radius: 0.3rem;
    padding: 0.2rem 0.5rem; }
  .chat__members {
    grid-area: u;
    background-color: #fff6e5;
    border: 0.1rem solid #ffecd1;
    border-radius: 0.3rem;
    overflow-y: auto; }
    .chat__members::-webkit-scrollbar {
      width: 0.75rem;
      background-color: inherit; }
    .chat__members::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 0.3rem rgba(0, 0, 0, 0.3);
      border-radius: inherit; }
    .chat__members::-webkit-scrollbar-thumb {
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: inherit; }
  .chat__messages {
    grid-area: m;
    background-color: #f2f2ff;
    border: 0.1rem solid #dedeff;
    border-radius: 0.3rem;
    overflow-y: auto; }
    .chat__messages::-webkit-scrollbar {
      width: 0.75rem;
      background-color: inherit; }
    .chat__messages::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 0.3rem rgba(0, 0, 0, 0.3);
      border-radius: inherit; }
    .chat__messages::-webkit-scrollbar-thumb {
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: inherit; }
  .chat__footer {
    grid-area: f;
    background-color: #e1ffe1;
    border: 0.1rem solid #cdffcd;
    border-radius: 0.3rem;
    display: flex; }
